@import url(https://cdnjs.cloudflare.com/ajax/libs/MaterialDesign-Webfont/4.7.95/css/materialdesignicons.min.css);
body {
    font-size: 100%;
    font-family: "Montserrat", sans-serif;
    background-color: #b3d3f6;
}

body,
#wrapper {
    min-height: 100vh;
}

h1 {
    font-weight: 700;
}

.white-text {
    color: white;
}

#home-section{
    margin: 50px auto;
}

.heading {
    text-align: center;
    margin: 20px;
    margin-bottom: 50px;
}

/* .container {
    margin: 10;
} */

.loader {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 70vh;
}

/* @import "https://cdnjs.cloudflare.com/ajax/libs/MaterialDesign-Webfont/4.7.95/css/materialdesignicons.min.css"; */

.main-image {
  height: 100vh;
  display: inherit;
  background-image: -webkit-gradient(
    linear,
    left top, right top,
    color-stop(50%, black),
    color-stop(50%, black),
    color-stop(50%, rgb(4, 138, 191)),
    color-stop(50%, #0072bc)
  );
  background-image: linear-gradient(
    to right,
    black 50%,
    black 50%,
    rgb(4, 138, 191) 50%,
    #0072bc 50%
  );
  width: 100%;
}

.main-text {
  color: #b3d3f6;
  text-transform: capitalize;
  font-size: clamp(2rem, 3vw, 3rem);
  text-shadow: 1px 2px 5px #000;
}

h1.main-text {
  font-weight: 400;
}

.main-title {
  font-size: clamp(3rem, 5vw, 5rem);
  text-transform: capitalize;
  color: #1490e4;
  margin-top: 0;
  font-weight: 900;
  padding: 1px;
  text-shadow: 1px 2px 5px #000;
}

.center-flex {
  display: flex;
  align-items: center;
  overflow: hidden;
}

.title-container {
  margin-left: 100px;
}

.bg-image {
  margin-left: 20%;
}
@media (max-width: 768px) {
  .main-image {
    height: 50vh;
    background-color: black;
    width: 100%;
    background-image: unset;
  }
  .title-container {
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    margin: 0;
  }
  .bg-image {
    display: none;
  }
  .center-flex {
    display: flex;
    align-items: center;
    overflow: hidden;
    height: 100vh;
  }
}

.nav {
  font-family: "Montserrat", sans-serif;
  height: 60px;
  display: flex;
  width: 100%;
  justify-content: center;
}
.nav .nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
}
.nav .nav-container .clubLogo {
  display: flex;
  align-items: center;
}
.nav .nav-container .clubLogo .logo {
  width: 70px;
  height: 60px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 60% 60%;
}
.nav .nav-container .clubLogo h1 {
  font-size: 25px;
}
.nav .nav-container .links {
  margin-right: 5px;
  width: 45%;
  display: flex;
  justify-content: space-between;
  z-index: 1;
}
.nav .nav-container .links a {
  font-size: 18px;
  font-weight: 500;
  text-decoration: none;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  padding: 5px 10px;
  border-radius: 5px;
  margin: 5px;
  overflow: hidden;
  position: relative;
  /* color: #1490e4; */
}
.nav .nav-container .links a::before {
  content: " ";
  position: absolute;
  top: 0%;
  left: 0%;
  width: 0%;
  height: 0;
  background-color: #1490e4;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  border-radius: 50%;
  z-index: -1;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.nav .nav-container .links a:hover::before {
  width: 300%;
  height: 300%;
}
.nav .nav-container .links a:hover {
  /* text-shadow: 2px 2px 4px #00000048; */
  color: white !important;
  /* background-color: #1490e4; */
}
.nav-sticky {
  top: 0;
  position: -webkit-sticky;
  position: sticky;
  z-index: 1;
}
.nav-fixed {
  top: 0;
  position: fixed;
  z-index: 1;
}

/* Styles for mobile nav */

.mnav {
  display: flex;
  height: 50px;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding: 0 15px;
  font-family: Montserrat, sans-serif;
}
.mnav .logo {
  width: 42px;
  height: 35px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
}
.mnav .mnav-menu {
  color: #1490e4;
  font-size: 30px;
}

@media screen and (max-width: 576px){
    .upcoming-events-container{
        padding: 0 !important;
    }
}
.EventCard {
  width: 350px;
  height: 470px;
  border-radius: 3px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
  overflow: hidden;
  font-size: 16px;
}
.EventCard .thumb {
  width: auto;
  height: 350px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border-radius: 3px;
}
.EventCard .infos {
  width: auto;
  height: 470px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 5px 24px;
  background: #fff;
  -webkit-transition: 0.6s 0.15s cubic-bezier(0.17, 0.67, 0.5, 1.03);
  transition: 0.6s 0.15s cubic-bezier(0.17, 0.67, 0.5, 1.03); 
}
.EventCard .title {
  padding-right: 40px;
}
.EventCard .infos .title {
  position: relative;
  margin: 7px 0;
  letter-spacing: 2px;
  color: #152536;
  font-size: 1em;
  font-weight: 700;
  text-transform: capitalize;
  text-shadow: 0 0 0px #32577f;
}
.EventCard .infos .time {
  position: absolute;
  top: 50%;
  right: 0;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  /* width: 35px; */
  text-transform: none;
  height: 23px;
  font-size: 0.9em;
  color: rgba(67, 83, 100, 0.7);
  background-size: 100% auto;
  display: inline-block;
}
.EventCard .infos .detail {
  margin-bottom: 10px;
  text-transform: uppercase;
  font-size: 0.85em;
  color: rgba(21, 37, 54, 0.7);
}
.EventCard .infos .txt {
  border-top: 1px solid rgba(0, 0, 0, 0.2);
  padding-top: 13px;
  line-height: 2;
  font-size: 0.95em;
  color: rgba(21, 37, 54, 0.7);
  opacity: 0;
  -webkit-transition: 0.5s 0.25s cubic-bezier(0.17, 0.67, 0.5, 1.03);
  transition: 0.5s 0.25s cubic-bezier(0.17, 0.67, 0.5, 1.03);
}
.EventCard .infos .details {
  padding-bottom: 19px;
  letter-spacing: 1px;
  color: #4e958b;
  font-size: 0.9em;
  text-transform: uppercase;
  cursor: pointer;
  opacity: 0;
  -webkit-transition: 0.5s 0.25s cubic-bezier(0.17, 0.67, 0.5, 1.03);
  transition: 0.5s 0.25s cubic-bezier(0.17, 0.67, 0.5, 1.03);
}

.EventCard .ellipss {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  line-height: 1.5em;
  max-height: 3em;
}

.EventCard:hover .ellipss {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 5;
  line-height: 1.5em;
  max-height: 7.5em;
}
.EventCard:hover .infos {
  -webkit-transform: translateY(-350px);
  transform: translateY(-350px);
}

.EventCard:hover .infos {
  overflow-y: auto;
}

.EventCard .infos::-webkit-scrollbar {
  width: 5px;
}

.EventCard .infos::-webkit-scrollbar-track {
  border-radius: 5px;
}

.EventCard .infos::-webkit-scrollbar-thumb {
  border-radius: 5px;
  box-shadow: inset 0 0 6px rgba(0,0,0,0.5);
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
}

.EventCard:hover .infos .seats,
.EventCard:hover .infos .txt,
.EventCard:hover .infos .details {
  opacity: 1;
}
.EventCard {
  text-align: left;
}

.past,
.upcoming {
  margin-bottom: 20px;
}

@media screen and (max-width: 600px) {
  .EventCard {
    width: 300px;
    height: 440px;
    font-size: 15px;
  }
  .EventCard .thumb {
    height: 300px;
  }

  .EventCard .infos {
    height: 440px;
  }
  .EventCard .infos .title {
    margin: 13px 0;
  }
  .EventCard .infos .time {
    font-size: 0.83em;
  }
  .EventCard .infos .detail {
    font-size: 0.82em;
  }
  .EventCard .infos .txt {
    font-size: 0.85em;
  }

  .EventCard:hover .infos {
    -webkit-transform: translateY(-300px);
    transform: translateY(-300px);
  }
}

